.project-text-c {
  position: relative;
  width:calc(100% -80px);
  display: flex;
  flex-direction: row;
  /* padding: 40px 40px 30px; */
  padding: 40px 0px;

  overflow: hidden;
  /* background: #181818; */
}

.project .col {
  display: flex;
  flex-direction: column;
  gap: 18px;
  flex: 1 1;
}
.project-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  gap: 24px;
}
#projects .arrow {
  width: 68px;
  height: 68px;
  transform: scale(.8) rotate(-90deg);
  /* flex: 1 1; */
