  margin-left: -10px;
}
.project-title {
font-family: Inter;
font-size: 24px;
font-weight: 300;
margin: 0;
color: #050505;
line-height: 1.6;
width: 100%;
}
.project-subtitle {
  font-family: Inter;
  font-size: 16px;
  font-weight: 300;
  margin: 0;
  color: #050505;
  opacity: .4;
  }


